.message-list {
  padding: 0 0.24rem 0.24rem;
}
.message-list .item {
  margin-top: 0.24rem;
}
.message-list .tap {
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  border-radius: 8px;
  padding: 0.24rem 0.24rem 0.24rem 0.8rem;
  background: #FFF;
  position: relative;
}
.message-list .tap:before {
  position: absolute;
  width: 0.16rem;
  height: 0.16rem;
  content: '';
  border-radius: 0.16rem;
  top: 0.4rem;
  left: 0.3rem;
}
.message-list .item:nth-child(1n) .tap:before {
  background: #96c15b;
}
.message-list .item:nth-child(2n) .tap:before {
  background: #f2ae40;
}
.message-list .item:nth-child(3n) .tap:before {
  background: #f8d64d;
}
.message-list .item:nth-child(4n) .tap:before {
  background: #4893eb;
}
.message-list .item:nth-child(5n) .tap:before {
  background: #5a5eca;
}
.message-list .item:nth-child(6n) .tap:before {
  background: #d3483e;
}
.message-list .content {
  width: 100%;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: space-between;
}
.message-list .name {
  font-size: 0.3rem;
  height: 0.48rem;
  line-height: 0.48rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.message-list .dsp {
  line-height: 0.36rem;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.24rem;
  -webkit-line-clamp: 2;
  color: #999;
  margin-top: 0.1rem;
}
.message-list .date {
  color: #CCC;
  font-size: 0.24rem;
  margin-top: 0.1rem;
}
