.running-time {
  padding: 0.24rem;
  background: #f6f6f6;
  position: -webkit-sticky;
  position: sticky;
  top: 44px;
  z-index: 10;
}
.running-time .date {
  font-size: 0.3rem;
  line-height: 0.36rem;
  margin-top: 0.06rem;
}
.running-time .info {
  font-size: 0.24rem;
  line-height: 0.3rem;
  color: #b2b2b2;
  margin-top: 0.08rem;
}
.running-list {
  background: #FFF;
  position: relative;
}
.running-list .itemsub {
  padding: 0.3rem;
  position: relative;
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
}
.running-list .itemsub:after {
  content: '';
  position: absolute;
  left: 0.3rem;
  right: 0;
  bottom: 0;
  height: 1px;
  background: #e5e8ea;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.running-list .itemsub:last-child:after {
  display: none;
}
.running-list .flag {
  width: 0.84rem;
  margin-right: 0.3rem;
  flex-shrink: 0;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: center;
}
.running-list .flag img {
  width: 100%;
  border-radius: 0.84rem;
}
.running-list .content {
  width: 100%;
}
.running-list .hd {
  height: 0.48rem;
  line-height: 0.48rem;
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
}
.running-list .name {
  width: 100%;
  font-size: 0.28rem;
  overflow: hidden;
}
.running-list .value {
  font-size: 0.36rem;
  flex-shrink: 0;
  margin-left: 0.18rem;
}
.running-list .time {
  height: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.24rem;
  margin-top: 0.12rem;
  color: #92999D;
}
