.helpList {
  background: #fff;
  margin-top: 0.2rem;
}
.helpList .item {
  position: relative;
}
.helpShow {
  padding: 0.3rem 0;
  background: #FFF;
}
.helpShowHd {
  padding: 0.18rem 0.24rem 0;
}
.helpShowHd .title {
  font-size: 0.36rem;
  line-height: 1.5;
  font-weight: bold;
}
.helpShowHd .info {
  line-height: 0.36rem;
  padding-top: 0.12rem;
  color: #96999f;
}
.helpContent {
  font-size: 0.3rem;
  line-height: 1.8;
  padding: 0.3rem;
}
.helpContent img {
  display: inline;
  max-width: 100%;
}
