/* Reset */
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6,
th {
  font-weight: normal;
  margin: 0;
}
img {
  border: 0;
  display: block;
}
input,
button,
textarea,
select {
  vertical-align: middle;
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  font-weight: inherit;
}
textarea {
  overflow: auto;
}
a {
  text-decoration: none;
}
:focus {
  outline: 0;
}
a,
.hand {
  cursor: pointer;
}
em,
i {
  font-style: normal;
}
ul li {
  list-style-type: none;
}
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
  border: none;
  padding: 0;
}
input:focus {
  outline: none;
}
input[type="button"],
input[type="text"],
input[type="password"],
input[type="submit"],
input[type="reset"],
input[type="search"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="time"],
input[type="number"],
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background: none;
  border-radius: 0 ;
}
input[type="radio"],
input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
}
/* Public */
html {
  min-height: 100%;
}
body {
  min-height: 100%;
  color: #3b4257;
  font-size: 12px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  /* Fix for webkit rendering */
  -webkit-text-size-adjust: 100%;
  /* Fix for webkit minFont size */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  /* Remove highlight on tap event*/
  -moz-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-tap-highlight-color: rgba(0, 0, 0, 0);
  -o-tap-highlight-color: rgba(0, 0, 0, 0);
  font: 12px/1.5 "Helvetica Neue", Helvetica, Arial, "Hiragino Sans GB", \5FAE\8F6F\96C5\9ED1, tahoma, simsun, \5b8b\4f53;
  -webkit-text-size-adjust: none;
  overflow-x: hidden;
  background: #f7f8fa;
}
a {
  color: #3b4257;
}
