.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
  font-size: 0 !important;
  line-height: 0!important;
  height: 0!important;
}
.clearfix:after {
  clear: both;
}
.clear {
  clear: both;
  display: block;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0;
}
